import React, { useEffect, /*useState*/ } from 'react';
import Bubbles from './code';

const Home = (props) => {
    //const [showBubbles, setShowBubbles] = useState(true);
    function refreshPage() {
        window.location.reload(false);
    }
    useEffect(() => {
        return () => {
            //console.log('return');
            //setShowBubbles(false);
            refreshPage();
        }
    });
    return (
        <div id="body">
            {/*showBubbles ?*/
                <Bubbles />
                /*:
                null*/
            }
        </div>
    )
}

export default Home;