import React, { useState, useEffect, } from 'react';
import { Input, Row, Col } from 'antd';
import { TR, } from '../../lang/types';
import { tr, en } from '../lang';

const { Search } = Input;

const SearchBox = ({ searchHandler, defaultValue, setPage, setCurrentPage, setActiveLink, Lang }) => {

    const [text, setText] = useState(en);

    useEffect(() => {
        if (Lang === TR) {
            setText(tr);
        }
        else { setText(en); }
    }, [Lang,]);

    return (
        <Row>
            <Col span={12} offset={6}>
                <Search
                    placeholder={text.search_bar}
                    enterButton={text.search}
                    size="large"
                    defaultValue={defaultValue}
                    onSearch={value => { searchHandler(value); setPage(1); setCurrentPage(1); setActiveLink(1); }} />
            </Col>
        </Row>
    )
};

export { SearchBox };