import { TR, EN, } from './types';

export const setLangTR = (params) => {
    return (dispatch) => {
        sessionStorage.setItem('lang', TR);
        dispatch({ type: TR });
    }
}
export const setLangEN = (params) => {
    return (dispatch) => {
        sessionStorage.setItem('lang', EN);
        dispatch({ type: EN });
    }
}
export const getLang = (params) => {
    return (dispatch) => {
        const lang = sessionStorage.getItem('lang');
        if (lang === TR) {
            dispatch({ type: TR });
        }
        else {
            dispatch({ type: EN });
        }
    }
}